.currencyController {
    position: relative;
    z-index: 1;
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    width: 100%;
    align-items: center;
    padding: 20px;
    margin: 10px auto;
    box-shadow: 0px 2px 1px #666;
    border: 2px solid #eee;
    background: #fff;
    transition: 0.5s ease-in-out;
}

.currencyController button {
    position: relative;
    width: 100%;
    flex: 0 0 100%;
    display: block;
    margin: 10px auto;
    padding: 20px 15px;

    overflow: hidden;

    border-width: 0;
    outline: none;
    border-radius: 2px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, .6);
    
    background-color: #2ecc71;
    color: #ecf0f1;
    
    transition: background-color .3s;
}

.currencyController button:hover {
    background-color: #27ae60;
    color: #fff;
    transition: 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}


.currencyController button:disabled {
    background: #DFDFDF;
    color: ##9F9F9F;
    pointer-events: none;
 }
 