.result-container {
    visibility: hidden;
    z-index: -1;
    top: 25%;
    position: absolute;
    width: 100%;
    max-height: 60px;
    padding: 20px;
    text-align: center;
    font-weight: bold;
    border: 3px solid #eee;
    background-color: #fff;
    box-shadow: 0px 2px 1px #666;
    transition: 0.5s ease-in-out;
    margin-bottom: 50px;
}

.result-container-show {
    z-index: 0;
    top: 10%;
    position: absolute;
    width: 100%;
    padding: 20px;
    text-align: center;
    font-weight: bold;
    border: 3px solid #eee;
    background-color: #fff;
    box-shadow: 0px 2px 1px #666;
    transition: 0.5s ease-in-out;
    margin-bottom: 50px;
}

.result-container-show {
    visibility: visible;
    top: 100%;
}

.error {
    color: red;
    font-weight: bold;
}