.select-wrapper {
  margin: 0 auto;
}

.select {
    display: block;
    width: 100%;
    padding: 15px;
    font-size: 16px;
    font-weight: bold;
    border: 3px solid #cce0f1;
    border-radius: 3px;
    background: url(https://cdnjs.cloudflare.com/ajax/libs/octicons/4.4.0/svg/chevron-down.svg) no-repeat 96% 50% #fff;
    min-height: 60px;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    outline: none;
    margin: 10px 0;
  }
  .select::-ms-expand {
    display: none;
  }
  
  .label {
    display: block;
    margin-bottom: .3em;
  }
  
  .mini-label {
    display: block;
    font-size: 0.8rem;
  }
  