/*------------∞
|  reset.css  |
∞------------*/
/* ∞∞ postcss ∞∞ */

/* ∞∞ font ∞∞ */
body {
	font-family: 'Lato', sans-serif;
}
/* ∞∞ global ∞∞ */
	* {
		position: relative;
		margin: 0;
		padding: 0;
		box-sizing: border-box;
		border-color: inherit;
	}

	body {
		width: 100vw;
		height: 100vh;
		overflow-x: hidden;
		overflow-y: scroll;
		font-size: 100%;
		font-style: normal;
		font-weight: 300;
		line-height: 1;
		margin: 0;
		padding: 0;
	}
	
/* ∞∞ text styling ∞∞ */
	h1, h2, h3, h4, h5, h6, p, em, a, b, strong {
		font-style: normal;
		font-weight: 300;
	}

	strong {
		font-size: 1.15em;
	}

	small {
  	font-size: .85em;
	}

	ul,
	ol,
	menu {
		list-style: none;
	}

	sub,
	sup {
  	font-size: .75em;
  	line-height: 0;
  	position: relative;
  	vertical-align: baseline;
	}
	
	sup {
  	top: -0.5em;
	}
	
	sub {
  	bottom: -0.25em;
	}

	code,
	kbd,
	pre,
	samp {
  	font-family: monospace, monospace;
  	font-size: 1em;
	}

	abbr[title] {
  	border-bottom: 1px dotted;
	}
	
	dfn {
  	font-style: italic;
	}

/* ∞∞ form elements ∞∞ */
	button,
	input,
	optgroup,
	select,
	textarea {
  	color: inherit;
  	font: inherit;
  	margin: 0;
  	background: transparent;
		outline: none;
		border: none;
		border-radius: 0;
		-webkit-appearance: none;
		   -moz-appearance: none;
		        appearance: none;
	}
	
	button,
	select {
  	text-transform: none;
  	cursor: pointer;
	}

	button[disabled],
	html input[disabled] {
  	cursor: default;
	}
	
	fieldset {
  	border: none;
  	margin: 0;
  	padding: 0;
		min-width: 0;
	}

	textarea {
		resize: none;
	}

/* ∞∞ link styling ∞∞ */
	a {
		display: inline;
		text-decoration: none;
		color: inherit;
		cursor: pointer;
	}

	a:active,
	a:hover {
  	outline: 0;
	}

	/* a::before {
		width: 100%;
		height: .2rem;
		position: absolute;
		bottom: 0;
		left: 0;
		background-color: #7ac;
	}

	a:hover::before {
		background-color: #49e;
		height: .35rem;
	} */

/* ∞∞ hr using box ∞∞ */
	hr {
		border: none;
  	box-sizing: border-box;
  	height: .075rem;
	}

/* ∞∞ hidden ∞∞ */
	[hidden],
	template {
  	display: none;
	}

/* ∞∞ media specific ∞∞ */
	audio:not([controls]) {
  	display: none;
  	height: 0;
	}

/* ∞∞ tables ∞∞ */
	table {
  	border-collapse: collapse;
  	border-spacing: 0;
  	border: none;
	}

	td,
	th {
		margin: 0;
  	padding: 0;
  	font-weight: inherit;
		text-align: inherit;
	}


/*====================================================== ∞∞ browser specific ∞∞ */

/* ∞∞ IE ∞∞ */
	article,
	aside,
	details,
	figcaption,
	figure,
	footer,
	header,
	main,
	menu,
	nav,
	section,
	summary {
  	display: block;
	}
	
	audio,
	canvas,
	progress,
	video {
  	display: inline-block;
  	vertical-align: baseline;
	}

	a {
  	background-color: transparent;
	}

	button {
  	overflow: visible;
	}

	html {
  	-ms-text-size-adjust: 100%;
	}

	img {
  	border: none;
	}

	input[type="checkbox"],
	input[type="radio"] {
  	box-sizing: border-box;
  	padding: 0;
	}

	legend {
  	border: none;
  	padding: 0;
	}

	mark {
  	background: #ff0;
  	color: #000;
	}

	svg:not(:root) {
  	overflow: hidden;
	}

	textarea {
  	overflow: auto;
	}

/* ∞∞ moz ∞∞ */
	body {
		-moz-osx-font-smoothing: grayscale;
	}

	button::-moz-focus-inner,
	input::-moz-focus-inner {
  	border: none;
  	padding: 0;
	}

	input {
  	line-height: normal;
	}

/* ∞∞ webkit ∞∞ */
	* {
		-webkit-margin-before: 0;
		-webkit-margin-after: 0;
	}

	html {
  	-webkit-text-size-adjust: 100%;
	}

	body {
		-webkit-font-smoothing: antialiased !important;
	}

	blockquote {
		-webkit-margin-before: 0;
		-webkit-margin-after: 0;
		-webkit-margin-start: 0;
		-webkit-margin-end: 0;
	}

	fieldset {
		-webkit-margin-start: 0;
		-webkit-margin-end: 0;
		-webkit-padding-before: 0;
		-webkit-padding-start: 0;
		-webkit-padding-end: 0;
		-webkit-padding-after: 0;
	}

	input[type=number]::-webkit-inner-spin-button, 
	input[type=number]::-webkit-outer-spin-button { 
  	-webkit-appearance: none; 
  	margin: 0; 
  	height: auto;
	}

	input[type="search"] {
  	-webkit-appearance: textfield;
  }

  input[type="search"]::-webkit-search-cancel-button,
	input[type="search"]::-webkit-search-decoration,
	progress {
  	-webkit-appearance: none;
	}
form {
	width: 100%;
	height: 100%;
	padding-bottom: 75px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-image: radial-gradient(at top, #b5e6cf, #9dddad);
}

*: {
	box-sizing: border-box;
}

.app-container {
	display: flex;
	flex-flow: column;
	align-items: center;
	width: 100%;
	margin-top: 20px;
}

.selectMode {
	padding: 20px 15px;
	font-weight: bold;
	background-color: #fff;
	color: #333;
	border: 3px solid #b5e6cf;
}

.selectMode:hover {
	display: block;
    background-color: #cce0f1;
    color: #fff;
    transition: 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
    font-weight: bold;
}

nav {
	position: relative;
    z-index: 1;
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    width: 100%;
    align-items: center;
    padding: 20px;
    margin: 0px auto;
    box-shadow: 0px 2px 1px #666;
    border: 2px solid #eee;
    background: #fff;
	transition: 0.5s ease-in-out;
	text-align: left;
}
.currencyController {
    position: relative;
    z-index: 1;
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    width: 100%;
    align-items: center;
    padding: 20px;
    margin: 10px auto;
    box-shadow: 0px 2px 1px #666;
    border: 2px solid #eee;
    background: #fff;
    transition: 0.5s ease-in-out;
}

.currencyController button {
    position: relative;
    width: 100%;
    flex: 0 0 100%;
    display: block;
    margin: 10px auto;
    padding: 20px 15px;

    overflow: hidden;

    border-width: 0;
    outline: none;
    border-radius: 2px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, .6);
    
    background-color: #2ecc71;
    color: #ecf0f1;
    
    transition: background-color .3s;
}

.currencyController button:hover {
    background-color: #27ae60;
    color: #fff;
    transition: 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}


.currencyController button:disabled {
    background: #DFDFDF;
    color: ##9F9F9F;
    pointer-events: none;
 }
 
.select-wrapper {
  margin: 0 auto;
}

.select {
    display: block;
    width: 100%;
    padding: 15px;
    font-size: 16px;
    font-weight: bold;
    border: 3px solid #cce0f1;
    border-radius: 3px;
    background: url(https://cdnjs.cloudflare.com/ajax/libs/octicons/4.4.0/svg/chevron-down.svg) no-repeat 96% 50% #fff;
    min-height: 60px;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    outline: none;
    margin: 10px 0;
  }
  .select::-ms-expand {
    display: none;
  }
  
  .label {
    display: block;
    margin-bottom: .3em;
  }
  
  .mini-label {
    display: block;
    font-size: 0.8rem;
  }
  
.result-container {
    visibility: hidden;
    z-index: -1;
    top: 25%;
    position: absolute;
    width: 100%;
    max-height: 60px;
    padding: 20px;
    text-align: center;
    font-weight: bold;
    border: 3px solid #eee;
    background-color: #fff;
    box-shadow: 0px 2px 1px #666;
    transition: 0.5s ease-in-out;
    margin-bottom: 50px;
}

.result-container-show {
    z-index: 0;
    top: 10%;
    position: absolute;
    width: 100%;
    padding: 20px;
    text-align: center;
    font-weight: bold;
    border: 3px solid #eee;
    background-color: #fff;
    box-shadow: 0px 2px 1px #666;
    transition: 0.5s ease-in-out;
    margin-bottom: 50px;
}

.result-container-show {
    visibility: visible;
    top: 100%;
}

.error {
    color: red;
    font-weight: bold;
}
